import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import {inject, observer} from "mobx-react";
import CatalogItem from "components/Catalog/CatalogItem";
import {getApartment} from "api/apartments";

const LikedApartments = ({ userStore }) => {

    const [apartments, setApartments] = useState([]);
    useEffect(() => {
        Promise.all(userStore.user.liked_apartments.map(id => getApartment(id)))
            .then(data => {
                setApartments(data);
            })
    }, [ userStore.user.liked_apartments ])
    return (
        <Container>
            {apartments.map(el => <CatalogItem key={el.id} apartment={el}/>)}
        </Container>
    )
}

export default inject('userStore')(observer(LikedApartments));

const Container = styled.div`
  min-height: 400px;
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  max-width: 1000px;
  margin: 50px auto;
  justify-content: center;
`;