import React, { useState, useEffect } from 'react';
import styled from "styled-components";
import { inject, observer } from "mobx-react";
import { getSubscription } from "api/subscriptions";
import { PrimaryButton } from "components/public/buttons";
import { routes } from "consts/routes";
import { Link } from "react-router-dom";

const MySubscriptions = ({ userStore }) => {
    const [ subscription, setSubscription ] = useState(null);

    useEffect(() => {
        if (userStore.user.subscription && userStore.user.hasSubscription) getSubscription(userStore.user.subscription)
            .then(data => {
                setSubscription(data);
            })
    }, [ userStore.user.hasSubscription, userStore.user.subscription ]);

    if (subscription) return (
        <Container>
            <h2>Ваш варіант підписки: {subscription.name}</h2>
            <h3>Вартість: {subscription.price} ₴</h3>
            <h4>Умови:</h4>
            <ul>{subscription.description?.trim().split('     ').map(el => <li>{el}</li>)}</ul>
        </Container>
    )

    return <NoSubscriptionsContainer>
        <h2>У Вас немає жодної активної підписки</h2>
        <p>Підписки потрібні для того, щоб дивитися контакти власників квартир, які ми відбираємо вручну</p>
        <Link to={routes.TARRIFS}>
            <PrimaryButton>Переглянути доступні варіанти підписок</PrimaryButton>
        </Link>
    </NoSubscriptionsContainer>

}




export default inject('userStore')(observer(MySubscriptions));

const Container = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 900px;
  width: 100%;
  margin: auto;
  
  h4 {
    margin-bottom: 0;
  }
`;

const NoSubscriptionsContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 300px;
  a {
    text-decoration: none;
    width: max-content;
    align-self: center;
  }
  button {
    width: max-content;
  }
`;